import { DetailedHTMLProps, TdHTMLAttributes, createElement } from 'react';
import cn from 'classnames';

import styles from '../Table.module.scss';

export interface CellProps extends
  DetailedHTMLProps<TdHTMLAttributes<HTMLTableDataCellElement>, HTMLTableDataCellElement> {

  /** Обрезать текст? */
  ellipsis?: boolean;

  /** Ячейка для заголовка или обычная? */
  component?: 'th' | 'td';
}

/** Базовая ячейка таблицы */
export function Cell({
  component = 'td',
  ellipsis,
  children,
  align = 'left',
  className,
  ...rest
}: CellProps): JSX.Element {
  const classes = cn(className, { [`${ styles.ellipsis_data }`]: ellipsis });

  return createElement(component, { ...rest, align, className: classes }, children);
}
